import { mapGetters } from "vuex";
export default {
  name: "login",
  data: () => ({
    username: "",
    password: "",
  }),
  computed: {
    ...mapGetters("Auth", ["isRequesting"]),
  },
  methods: {
    onSubmit() {
      const formData = {
        username: this.username,
        password: this.password,
      };

      this.$store.dispatch("Auth/requestLogin", formData);
    },
  },
};
