import EntityList from "../../components/Entity/EntityList";
import { mapActions, mapGetters, mapMutations } from "vuex";

export default {
  components: { EntityList },
  data: () => ({
    menuProps: {
      bottom: true,
      offsetY: true,
      tile: true,
    },
    headers: [
      {
        text: "Название",
        align: "start",
        sortable: false,
        value: "name",
      },
      {
        text: "Город",
        align: "start",
        sortable: false,
        value: "cityId.name",
      },
      {
        text: "Адрес",
        align: "start",
        sortable: false,
        value: "address",
      },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
  }),
  created() {
    this.getPoints();
    this.getCities();
  },
  methods: {
    ...mapMutations("Point", ["resetPoints"]),
    ...mapActions("Point", [
      "requestPoints",
      "createPoint",
      "updatePoint",
      "deletePoint",
      "setCurrentPage",
    ]),
    ...mapActions("City", ["requestCities"]),
    async getPoints() {
      const response = await this.requestPoints();
    },
    async getCities() {
      const response = await this.requestCities({ limit: null });
    },
    handlePageChange(value) {
      this.setCurrentPage(value);
    },
    create(point) {
      this.createPoint({ point });
    },
    update(point) {
      this.updatePoint({ id: point.id, point });
    },
    remove(point) {
      this.deletePoint({ id: point.id });
    },
  },
  computed: {
    ...mapGetters("Point", ["isRequesting", "points", "pagination"]),
    ...mapGetters("City", ["isRequesting", "cities"]),
  },
  beforeDestroy() {
    this.resetPoints();
  },
};
