import ControlButtons from "@/components/ControlButtons";
import { mapGetters } from "vuex";

export default {
  props: {
    items: Array,
    headers: Array,
    loading: Boolean,
  },
  components: { ControlButtons },
  data: () => ({
    dialog: false,
    dialogDelete: false,
    editedItem: {},
    defaultItem: {},
  }),
  methods: {
    editItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$emit("onRemove", this.editedItem);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },

    onSubmit() {
      if (this.isEdit) {
        this.$emit("onUpdate", this.editedItem);
      } else {
        this.$emit("onCreate", this.editedItem);
      }
      this.close();
    },
  },
  computed: {
    ...mapGetters(["isDataRequesting"]),
    isEdit() {
      return Boolean(this.editedItem.id);
    },
    formTitle() {
      return !this.isEdit ? "Создать" : "Редактировать";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
};
