import { mapActions, mapGetters, mapMutations } from "vuex";
import EntityList from "../../components/Entity/EntityList";

export default {
  components: { EntityList },
  data: () => ({
    headers: [
      {
        text: "Название",
        align: "start",
        sortable: false,
        value: "name",
      },
      {
        text: "Описание",
        align: "start",
        sortable: false,
        value: "description",
      },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
  }),
  created() {
    this.getCategories();
  },
  methods: {
    ...mapMutations("Category", ["resetCategories"]),
    ...mapActions("Category", [
      "requestCategories",
      "createCategory",
      "updateCategory",
      "deleteCategory",
      "setCurrentPage",
    ]),
    async getCategories() {
      const response = await this.requestCategories();
    },
    handlePageChange(value) {
      this.setCurrentPage(value);
    },
    create(category) {
      this.createCategory({ category });
    },
    update(category) {
      this.updateCategory({ id: category.id, category });
    },
    remove(category) {
      this.deleteCategory({ id: category.id });
    },
  },
  computed: {
    ...mapGetters("Category", ["isRequesting", "categories", "pagination"]),
  },
  beforeDestroy() {
    this.resetCategories();
  },
};
