import EntityList from "../../components/Entity/EntityList";
import { mapActions, mapGetters, mapMutations } from "vuex";

export default {
  components: { EntityList },
  data: () => ({
    menuProps: {
      bottom: true,
      offsetY: true,
      tile: true,
    },
    headers: [
      {
        text: "Название",
        align: "start",
        sortable: false,
        value: "name",
      },
      {
        text: "Длительность",
        align: "start",
        sortable: false,
        value: "unit",
      },
      {
        text: "Цена",
        align: "start",
        sortable: false,
        value: "price",
        width: "10%",
      },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
  }),
  created() {
    this.getRates();
    this.getUnits();
  },
  methods: {
    ...mapMutations("Rate", ["resetRates"]),
    ...mapActions("Rate", [
      "requestRates",
      "createRate",
      "updateRate",
      "deleteRate",
      "setCurrentPage",
    ]),
    ...mapActions("Unit", ["requestUnits"]),
    async getRates() {
      const response = await this.requestRates();
    },
    async getUnits() {
      const response = await this.requestUnits({ limit: null });
    },
    handlePageChange(value) {
      this.setCurrentPage(value);
    },
    create(rate) {
      this.createRate({ rate });
    },
    update(rate) {
      this.updateRate({ id: rate.id, rate });
    },
    remove(rate) {
      this.deleteRate({ id: rate.id });
    },
    getRateText(item) {
      return `${item.name} (${item.unit})`;
    },
  },
  computed: {
    ...mapGetters("Rate", ["isRequesting", "rates", "pagination"]),
    ...mapGetters("Unit", ["isRequesting", "units"]),
  },
  beforeDestroy() {
    this.resetRates();
  },
};
