export default {
  props: {
    accept: {
      type: Boolean,
      default: false,
    },
    remove: {
      type: Boolean,
      default: false,
    },
    update: {
      type: Boolean,
      default: true,
    },
  },
  data: () => ({
    buttonToggleState: [],
  }),
  watch: {
    buttonToggleState(n, o) {
      if (JSON.stringify(n) == JSON.stringify(o)) return;
      this.buttonToggleState = [];
    },
  },
};
