export default {
  props: {
    imagePath: String,
  },
  data: () => ({
    acceptedImageTypes: ["image/jpeg", "image/png"],
    path: null
  }),
  created(){
    this.path = this.imagePath
  },
  methods: {
    onLoadImage(event) {
      this.$emit("onUpload", event, this.acceptedImageTypes);
    },
  },
};
