import Filters from "@/components/Filters";
import ControlButtons from "@/components/ControlButtons";
import { mapActions, mapGetters, mapMutations } from "vuex";

export default {
  components: { Filters, ControlButtons },
  data: () => ({
    dialog: false,
    dialogDelete: false,
    filters: [
      { id: "categoryId", name: "Категория", values: [], selectedValue: null },
    ],
    filterParams: {},
    editedItem: {},
    defaultItem: {},
    headers: [
      {
        text: "Изображение",
        align: "start",
        sortable: false,
        value: "thumbnail",
      },
      {
        text: "Название",
        align: "start",
        sortable: false,
        value: "name",
      },
      {
        text: "Описание",
        align: "start",
        sortable: false,
        value: "description",
      },
      {
        text: "Мин. Цена",
        align: "start",
        sortable: false,
        value: "priceMin",
      },
      {
        text: "Макс. Цена",
        align: "start",
        sortable: false,
        value: "priceMax",
      },
      {
        text: "Номер",
        align: "start",
        sortable: false,
        value: "number",
      },
      {
        text: "Доступные цвета",
        align: "start",
        sortable: false,
        value: "colors",
      },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
  }),
  created() {
    this.getCars();
  },
  mounted() {
    this.setFilters();
  },
  computed: {
    ...mapGetters("Car", ["isRequesting", "cars", "pagination"]),
    ...mapGetters("Category", ["categories"]),
  },
  methods: {
    ...mapMutations("Car", ["resetCars"]),
    ...mapActions("Car", [
      "setCurrentPage",
      "requestCars",
      "deleteCarFromList",
      "setFilter",
      "resetFilter",
    ]),
    ...mapActions("Category", ["requestCategories"]),
    async getCars(params = {}) {
      const response = await this.requestCars(params);
    },
    async setFilterCategories() {
      const response = await this.requestCategories({ limit: null });
      this.filters = this.filters.map((item) => {
        if (item.id == "categoryId") {
          item.values = this.categories;
        }
        return item;
      });
    },
    setFilters() {
      this.setFilterCategories();
    },
    handleRequestFilters() {
      this.filters.map((filter) => {
        this.filterParams[filter.id] = filter.selectedValue;
      });
      this.setFilter(this.filterParams);
    },
    handleResetFilters() {
      this.filters.map((filter) => {
        filter.selectedValue = null;
      });
      this.filterParams = {};
      this.resetFilter();
    },
    handlePageChange(value) {
      this.setCurrentPage(value, this.filterParams);
    },
    deleteItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      const car = this.editedItem;
      if (car) {
        this.deleteCarFromList({ id: car.id });
        this.closeDelete();
      }
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    editItem(item) {
      this.$router.push({
        name: "EditCar",
        params: {
          id: item.id,
          carName: item.name,
        },
      });
    },
  },
  beforeDestroy() {
    this.resetCars();
  },
};
